<template>
  <div class="head">
    <van-nav-bar v-if="isShowTop" class="top" :class="[{'active': !isSubHeader}]" :title="title" left-text="">
      <template #left>
        <img class="back" src="@/assets/images/back@2x.png" @click="$goBack" alt="">
        <img v-if="isClose" class="back" src="@/assets/images/closer@2x.png" @click="closeWeb" alt="">
      </template>
      <template #right>
        <slot name="rightText"></slot>
      </template>
    </van-nav-bar>
    <van-nav-bar v-if="isSubHeader" left-text="" :class="[{'scroll': headerActive},{'bottom':isShowTop && isSubHeader}]" left-arrow>
      <template #left>
        <img src="@/assets/images/more.png" @click="onMenu" alt="">
      </template>
      <template #title>
        <img class="logo" src="@/assets/images/logo.png" alt="">
        <span>UVSwap</span>
      </template>
      <template #right>
        <!-- <div class="wallet-block flex-center">
          <span v-if="address" class="address flex-center">***{{address.substring(address.length-3, address.length)}}</span>
          <span v-else class="address flex" @click="initWallet">{{$t('web3.連接錢包')}}</span>
        </div> -->
        <van-popover class="lang-wrap" v-model:show="showLang" :actions="langList" placement="bottom-end">
          <div class="select">
            <div class="option" :class="{'active':local === item.lang}" v-for="(item,index) in langList" :key="index" @click="onSelectLang(item)">{{item.text}}</div>
          </div>
          <template #reference>
            <img style="margin-left:12px;" src="@/assets/images/lang@2x.png" alt="">
          </template>
        </van-popover>
      </template>
    </van-nav-bar>
    <!-- 菜单栏 -->
    <div class="menu-Block" :class="[{'open':open},{'isSubHeader':isShowTop && isSubHeader}]" @click.self="closeMenu()">
      <div class="menu-leftBlock">
        <div class="address-status">
          <div class="top">
            <div class="left ">{{address}}</div>
            <div class="right"><img class="copy clip-menu-button" :data-clipboard-text="address" src="@/assets/images/copy-default@2x.png" alt=""></div>
          </div>
          <div class="bottom">
            <div class="img-wrap"><img class="status" src="@/assets/images/radio-menu@2x.png" alt=""></div>
            <div class="text warning" v-if="isActive">{{$t('index.已啟動')}}</div>
            <div class="text" v-else>{{$t('index.未啟動')}}</div>
          </div>
        </div>
        <div class="menu-item flex-between" @click="IndexHandle">
          <div class="left flex-middle">
            <img class="menu-img" src="@/assets/images/pool@2x.png">
            <div class="menu-tit">{{$t('index.礦池')}}</div>
          </div>
          <div class="right"><img class="arrow" src="@/assets/images/arrow_r@2x.png" alt=""></div>
        </div>
        <div class="menu-item flex-between" @click="$router.push({name: 'Wallet'})">
          <div class="left flex-middle">
            <img class="menu-img" src="@/assets/images/kcInfo.png">
            <div class="menu-tit">{{$t('index.UV礦池信息')}}</div>
          </div>
          <div class="right"><img class="arrow" src="@/assets/images/arrow_r@2x.png" alt=""></div>
        </div>
        <div class="menu-item flex-between" @click="openWebview('/#/swap/')">
          <div class="left flex-middle">
            <img class="menu-img" src="@/assets/images/swap@2x.png">
            <div class="menu-tit">{{$t('index.Swap')}}</div>
          </div>
          <div class="right"><img class="arrow" src="@/assets/images/arrow_r@2x.png" alt=""></div>
        </div>
        <div class="menu-item flex-between" @click="$router.push({name:'TokenUpload'})">
          <div class="left flex-middle">
            <img class="menu-img" src="@/assets/images/upload-token@2x.png">
            <div class="menu-tit">{{$t('index.申請上幣')}}</div>
          </div>
          <div class="right"><img class="arrow" src="@/assets/images/arrow_r@2x.png" alt=""></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useRouter, onBeforeRouteLeave } from 'vue-router'
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import web3Data from "@/utils/initWeb3";
import { getLang, setLang } from "@/utils/i18n";
import { Toast } from 'vant';
import Clipboard from 'clipboard'
import config from "@/utils/config";
import UvStakeAbi from "@/utils/UvStakeAbi";
export default {
  components: {
  },
  props: {
    Headtype: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    isSubHeader: {
      type: Boolean,
      default: false
    },
    isShowTop: {
      type: Boolean,
      default: true
    },
    isClose: {
      type: Boolean,
      default: false
    }
  },
  setup (props) {
    const store = useStore()
    const { t } = useI18n()
    const router = useRouter()
    // const route = useRoute
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      open: false,
      Headtype: props.Headtype,
      headerActive: 0,
      title: props.title,
      local: getLang().toLowerCase() || "tw",
      isSubHeader: props.isSubHeader,
      showLang: false,
      isActive: false,
      langList: [
        { text: '繁體', lang: 'tw' },
        { text: 'English', lang: 'en' }
      ],
      changeLanuage: (val) => {
        setLang(val)
        data.local = val
      },
      initContract: () => {
        data.UvStakeContract = new data.web3.eth.Contract(UvStakeAbi, config.uvStakeAddress)
        data.initData()
      },
      initData: () => {
        data.getUserInfo()
      },
      initWallet: () => {
        web3Data.initWeb3()
      },
      getUserInfo: async () => {
        let userInfo = await data.UvStakeContract.methods.getUserInfo(data.address).call()
        data.isActive = userInfo.isExist
      },
      onMenu: () => {
        let flag = !data.open
        data.open = flag
        document.querySelector('body').style.overflow = flag ? 'hidden' : 'auto'
      },
      openWebview: (url) => {
        // if (url === '/#/swap/') {
        //   url = location.origin + '/swap' + url;
        // }
        // router.push({ name: 'Webview', query: { href: url } })
        // document.querySelector('body').style.overflow = 'auto'
        // data.open = false
        window.location.href = 'https://uvswap.github.io/swap/#/swap'
        // window.location.href = 'https://uvswap-p.dsceshi.cn/swap/#/swap'
      },
      closeWeb: () => {
        router.replace({ name: 'Index' })
        document.querySelector('body').style.overflow = 'auto'
        data.open = false
      },
      closeMenu: () => {
        document.querySelector('body').style.overflow = 'auto'
        data.open = false
      }
    })
    onMounted(() => {
      var clipboard = new Clipboard('.clip-menu-button')
      clipboard.on('success', function (e) {
        Toast(t('share.複製成功'))
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast(t('share.複製失敗，請手動長按複製'))
      })
    })
    document.addEventListener('scroll', function () {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      data.headerActive = scrollTop > 30
    })
    if (data.address) {
      data.initContract()
    }
    const onSelectLang = (action) => {
      if (action.lang) {
        data.changeLanuage(action.lang)
      }
      data.showLang = false
    }
    const IndexHandle = () => {
      if (router.currentRoute['_value'].name === 'Index') {
        data.open = false
        document.querySelector('body').style.overflow = 'auto'
      } else {
        router.push({ name: 'Index', query: {} })
      }
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    onBeforeRouteLeave(() => {
      document.querySelector('body').style.overflow = 'auto'
      data.open = false
    })
    const refData = toRefs(data);
    return {
      ...refData,
      onSelectLang,
      IndexHandle
    };
  }
}
</script>
